let scan = null;
export default {
    data() {
        return {
            codeUrl: '',
        }
    },
    mounted() {
        this.startRecognize(); //创建控件
        this.startScan(); //开始扫描
    },
    watch:{
        codeUrl(){
            if (!window.plus) return;
            scan.close();
        }
    },
    methods: {
        back(){
            this.$router.go(-1);
            if (!window.plus) return;
            scan.close();
        },
        //创建扫描控件
        startRecognize() {
            let filter;
            let styles = { frameColor: "#29E52C",scanbarColor: "#29E52C",background: "" };
            let that = this;
            if (!window.plus) return;
            scan = new plus.barcode.Barcode('bcid',filter,styles);
            scan.onmarked = onmarked;

            function onmarked(type, result, file) {
                switch (type) {
                    case plus.barcode.QR:
                        type = 'QR';
                        break;
                    case plus.barcode.EAN13:
                        type = 'EAN13';
                        break;
                    case plus.barcode.EAN8:
                        type = 'EAN8';
                        break;
                    default:
                        type = '其它' + type;
                        break;
                }
                result = result.replace(/\n/g, '');
                that.codeUrl = "#/mine/allowLogin" + '?id=' +  result;
                window.location.href = that.codeUrl;
                // alert(that.codeUrl)
                that.cancelScan();
                that.closeScan();
            }
        },
        //关闭扫描
        cancelScan() {
            if (!window.plus) return;
            scan.cancel();
        },
        //关闭条码识别控件
        closeScan() {
            if (!window.plus) return;
            scan.close();
        },
        //开始扫描
        startScan() {
            if (!window.plus) return;
            scan.start();
        },
    }
}



